body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #3A0088;
  font-family: 'SubZER0';  
}

@font-face {
  font-family: 'Gears of Peace';
  src: url(/giphyreact/static/media/GearsofPeace.d20c540a.woff2) format('woff2'),
      url(/giphyreact/static/media/GearsofPeace.57fd7db5.woff) format('woff');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'SubZER0';
  src: url(/giphyreact/static/media/SubZER0.8a1cdcca.woff2) format('woff2'),
      url(/giphyreact/static/media/SubZER0.a363bb45.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

.App {
  font-family: 'SubZER0';  
  display: grid;
  grid-template-columns: 5rem 1fr 5rem;
  grid-auto-rows: minmax(5rem, auto);
}

@media (max-width: 420px) {
  .App {
    grid-template-columns: 1fr;
  }
}
.Error {
  grid-row-start: 3;
  grid-column-start: 2;
  color: #FFBD39;
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  width: 20%;
  height: auto;
}

p {
    display: block;
    font-size: 3rem;
}

.NoMatch {
  grid-row-start: 3;
  grid-column-start: 2;
  color: #ffbd39;
  display: flex;
  justify-content: center;
  align-items: center;
}

.NoMatch img {
  width: 20rem;
  height: 20rem;
  height: auto;
}

.NoMatch p {
  display: block;
  font-size: 3rem;
}

.Home {
  grid-row-start: 3;
  grid-column-start: 2;
  color: #ffbd39;
  display: grid;
  justify-content: center;
  justify-items: center;
}

.Home p {
  margin: 1rem;
  width: 50%;
  height: auto;
  justify-self: center;
}

.Title {
    color: #FFBD39;
    grid-column: 1/1;
    grid-row: 1/1;
    align-self: center;
    justify-self: center;
    font-family: 'Gears of Peace';
    font-size: 5rem;
    text-align: center;
}

.link {
    color: inherit;
    text-decoration: none;
}
.FavoritedBtn{
  grid-column: 1/1;
  grid-row:2/2;
  color: #FFBD39;
  justify-self: right;
  border: none;
  background: transparent;
  font-family: "SubZER0";
  font-size: 1.5rem;
  z-index: 1;
  width: 10rem;
}

.FavoritedBtn a {
  color: #FFBD39;
}

.FavoritedBtn .selected {
  color: #E61C5D;
}

.Header {
    grid-column: 2/3;
    grid-row: 1/1;
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(3rem, auto);
    align-items: center;
}
.Results {
  grid-row-start: 3;
  grid-column-start: 2;
  justify-self: center;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 14rem);
  grid-gap: 3rem;
  grid-auto-rows: 15rem;
  grid-auto-columns: auto;
  grid-auto-flow: row;
  justify-content: center;
}
.ResultImage {
border: 0.5rem solid #FFBD39;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.favorited {
  border: 0.5rem solid #E61C5D;
}

.Result:hover .Modal{
  opacity: 1;
}
.Modal {
  background-color: rgba(251, 248, 254, 0.45);
  position: absolute;
  z-index: 1;
  width: 224px;
  height: 240px;
  border: 0.5rem solid transparent;
  display: grid;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.5s ease
}

.FavBtn {
  background-color: transparent;
  border: none;
  background-image: url("https://image.noelshack.com/fichiers/2018/09/2/1519761895-coeur.png");
  display: inline-block;
  width: 3rem;
  height: 3rem;
  background-size: 100%;
  z-index: 2;
  transition: -webkit-transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
}

.FavBtn:hover {
  -webkit-transform: scale(2, 2);
          transform: scale(2, 2);
}

.FavBtn a {
  color: #930077;
  font-size: 1.5rem;
}
.Search {
  grid-column: 2/3;
  grid-row: 2/2;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 3rem;
  color: #FFBD39;
}

.SearchBar {
  align-self: center;
  justify-self: center;
}

input {
  outline: none;
  display: block;
  border: 5px #FFBD39 solid;
  background: transparent;
  width: 15.5rem;
  height: 2.5rem;
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  position: relative;
  overflow: auto;
  font-family: 'SubZER0';
  font-size:1.5rem;
  color: #FFBD39;
}

.SubmitBtn {
  background: transparent;
  display: inline-block;
  background-image: url('https://image.noelshack.com/fichiers/2018/09/1/1519661192-export-3.png');
  background-size: 100%;
  margin: 0.8rem 0.3rem 0.5rem 0.5rem;
  width: 1rem;
  height: 1rem;
  background-repeat: no-repeat;
  position: absolute;
  border: none;
  z-index: 1;
}
